const PROCESS_TYPE = "process.Process";

import componentService from "../service/component";

function hasInjections(component)
{
    const descriptor = componentService.getComponents()[component.name];
    if (descriptor && (descriptor.dataProvider || descriptor.queries))
    {
        return true;
    }

    const kids = component.kids;
    if (kids)
    {
        for (let i = 0; i < kids.length; i++)
        {
            const kid = kids[i];

            if (hasInjections(kid))
            {
                return true;
            }
        }
    }
    return false;
}


module.exports = {
    isProcess: function(model)
    {
        return model && model.type === PROCESS_TYPE
    },

    isView: function(model)
    {
        return model && model.type === "view.View";
    },

    hasInjections: function(viewModel)
    {
        const content = viewModel.content;

        for (let name in content)
        {
            if (content.hasOwnProperty(name))
            {
                let c = content[name];

                if (hasInjections(c))
                {
                    return true;
                }
            }
        }
        return false;
    }
};



