import { createStore, applyMiddleware } from 'redux'

import rootReducer from "./reducers"
import thunk from 'redux-thunk'

function logger(store)
{
    return next => action => {

        console.log(action.type, {action});
        
        return next(action);
    }
}

export default function(initialState, api)
{
    return createStore(
        rootReducer,
        initialState,
        applyMiddleware(
            api !== undefined ? thunk.withExtraArgument(api) : thunk,
            logger
        )
    );
}



