import assign from "object-assign"

import { VIEW_UPDATE } from "../actions"

export default function view(state, action)
{
    const { type } = action;

    if (type === VIEW_UPDATE)
    {
        // merge update with current state
        const newState = assign(
            {},
            state,
            action.viewData
        );

        // merge meta separately
        newState.meta = assign({}, state.meta, action.viewData.meta);

        return newState;
    }

    return state;
}



