/** @jsx jsxToModel */
import jsxToModel from "../util/jsx-to-model"

import uuidV4 from "uuid/v4";

import { previewView, refetchView, updateView } from "../actions/view"
import { getViewModel } from "../reducers/model"

import i18n from "../service/i18n"
import store from "../service/store"

const MAX_HISTORY = 30;

const states = new Array(MAX_HISTORY);

const baseId = "-" + uuidV4();
let count = 1, idCount = 0;
let currentState = newEntry(nextId(), null);
states[0] = currentState;

function insert(state)
{
    states[count++] = newEntry(nextId(), state);
    if (count === MAX_HISTORY)
    {
        count = 0;
    }
}

function nextId()
{
    return (idCount++) + baseId;
}

function newEntry(id, state)
{
    currentState = {
        id: id,
        redux: state
    };

    return currentState;
}

function historyNotFound(id)
{
    const title = i18n('History Not Found', id);

    return (
        {
            title: JSON.stringify(title),
            content: {
                root: (
                    <div className="error-report">
                        <h1>{ title }</h1>
                        <p>
                            { i18n('History Not Found Explanation') }
                        </p>
                    </div>
                )
            }
        }
    );
}

const HistoryService = {

    states : states,

    update: function (state)
    {
        state = state || store.getState();

        currentState.redux = state;

        //console.log("UPDATE", currentState);

        const viewModel = getViewModel(state);
        window.history.replaceState( currentState.id, viewModel.title, window.location.href);
    },
    newState: function (state, url)
    {
        state = state || store.getState();

        insert(state);

        //console.log("INSERT", currentState);

        const viewModel = getViewModel(state);
        window.history.pushState( currentState.id , viewModel.title, url || window.location.href);
    },
    lookupEntry: function (id)
    {
        //console.log("STATES", states);

        for (let i = 0; i < states.length; i++)
        {
            const entry = states[i];
            if (entry && entry.id === id)
            {
                return entry;
            }
        }

        return null;
    }
};

export default HistoryService;

if (typeof window !== "undefined")
{
    window.onpopstate = function (ev)
    {
        const id = ev.state;

        const entry = HistoryService.lookupEntry(id);

        if (entry)
        {
            currentState = entry;
            
            store.dispatch(
                updateView(entry.redux)
            );
        }
        else
        {
            console.log("REFETCH");

            store.dispatch(
                refetchView()
            ).then(() => {

                //console.log("REFETCH THEN");
                insert(id, store.getState());
            });
            // store.dispatch(
            //     previewView( historyNotFound(id) )
            // );
        }
    };
}



