function handleText(elem)
{
    if (typeof elem !== "object")
    {
        return {
            name: "[String]",
            attrs: {
                value: String(elem)
            }
        };
    }
    return elem;
}

export default function jsxToModel(name, attrs)
{
    return {
        name,
        attrs,
        kids : Array.prototype.slice.call(arguments, 2).map(handleText)
    }
}



