
import assign from "object-assign"

import { SCOPE_SET } from "../actions/scope"
import { getDomain } from "./index"

export default function(scope = null, action)
{
    if (action.type === SCOPE_SET)
    {
        const changed = action.changed;

        const newScope = {
            graph: action.newGraph,
            dirty: scope.dirty
        };
        // was the scope value not marked dirty before?
        if (!scope.dirty[changed])
        {
            // mark dirty
            newScope.dirty = assign({}, scope.dirty);
            newScope.dirty[changed] = true;
        }
        return newScope;
    }

    return scope;
}

export function getScopeGraph(state)
{
    return state.scope.graph;
}

export function getScopeDirty(state)
{
    return state.scope.dirty;
}

export function getScopeInfo(state)
{
    return state.meta.scopeInfo;
}

export function getViewContext(state)
{
    return getScopeInfo(state).viewContext;
}



