import ajax from "../service/ajax"
import assign from "object-assign"

import HistoryService from "../service/history"

import { hasInjections } from "../util/model";

import { updateViewModel } from "./model";

export const VIEW_UPDATE = "VIEW_UPDATE";

export function updateView(data)
{
    return {
        type: VIEW_UPDATE,
        viewData: data
    };
}

const FETCH_DEFAULT_OPTS = {
    preview: null
};

function fetch(opts)
{
    opts = assign({}, FETCH_DEFAULT_OPTS, opts);

    const isPreview = !!opts.preview;

    const ajaxOpts = {
        url: opts.url || window.location.href,
        contentType: "application/json"
    };

    if (isPreview)
    {
        ajaxOpts.method =  "POST";
        ajaxOpts.data = opts.preview;
        ajaxOpts.headers = {
            "X-ceed-Preview" : "true"
        }
    }
    else
    {
        ajaxOpts.method =  opts.method;
        ajaxOpts.contentType = "application/json";
        ajaxOpts.data = opts.data || {};
        ajaxOpts.dataType = opts.dataType;
    }

    return ajax(ajaxOpts);
}


export function refetchView()
{
    return (dispatch, getState) => {

        return fetch({
                method: "GET",
                url: window.location.href,
                dataType: "XHR"
            })
            .then(function(xhr)
            {
                const data = JSON.parse(xhr.responseText);
                HistoryService.update(data);

                return dispatch(
                    updateView(data)
                );
            })
            .catch(function ()
            {
                console.error(err);
            });

    }
}


const NAVIGATE_TO_DEFAULTS = {
    progressive: true,
    urlProvider: (xhr, data) => {
        return xhr.responseURL;
    }
};


export function navigateView(opts)
{
    return (dispatch, getState) =>
    {
        opts = assign({}, NAVIGATE_TO_DEFAULTS, opts);

        const url = opts.url;

        if (!url)
        {
            return Promise.reject(new Error("No url given"));
        }

        if (!opts.progressive)
        {
            window.location.href = opts.url;
            return new Promise(function (resolve, reject)
            {
                //
                // This promise will never actually resolve. It is meant to convey to the caller that the full view change
                // operation is not done yet.
                //
                // When the page change happens, there will be a brand new js context.
                //
                // We add a long timeout to be sure the location change does not fail silently
                //
                window.setTimeout(
                    () => reject(new Error("Non progressive page load timeout")),
                    5000
                );
            });
        }

        return fetch({
                method: "GET",
                url: url,
                dataType: "XHR",
            })
            .then((xhr) =>
            {
                //console.log("URL", url, xhr.responseURL, xhr.responseText);
                const data = JSON.parse(xhr.responseText);
                const url = opts.urlProvider(xhr, data);

                HistoryService.update();
                HistoryService.newState(data, url);

                dispatch(
                    updateView(data)
                );
            })
            .catch(function (err)
            {
                console.error(err);
            });
    }
}

/**
 * Dev-Mode action creator to preview a new view model version for the current view.
 *
 */
export function previewView(model)
{
    return (dispatch, getState) =>
    {
        if (!hasInjections(model))
        {
            return dispatch(
                updateViewModel(model)
            )
        }

        return fetch({preview: model}).then(function (viewData)
        {
            // if it is an error
            if (viewData.error)
            {
                // resolve to error data
                return viewData;
            }

            // otherwise update the view with the preview view
            return dispatch(
                updateView(viewData)
            );
        });
    }
}

function createErrorViewModel(err)
{
    return {
        content: {
            main: {
                name: "Error",
                    attrs: {
                    "error" : err
                }
            }
        }
    };
}

export function showError(err)
{
    updateView(
        createErrorViewModel(err)
    )
}




